@media (prefers-color-scheme: dark) {
  body {
    color: white;
    background-color: black;
  }
}

@media (prefers-color-scheme: light) {
  body {
    color: black;
    background-color: white;
  }
}

body {
  margin-left: 10%;
  margin-right: 10%;
}

header {
  padding-top: 1cm;
  padding-bottom: 1cm;
}

fieldset {
  padding-top: 0.5cm;
  padding-bottom: 0.3cm;
}